@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Odibee+Sans&display=swap');
@import url('https://fonts.cdnfonts.com/css/ica-rubrik-black');
@import url('https://fonts.cdnfonts.com/css/poppins');
@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Odibee+Sans&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  background: #CBCBCB;
  min-height: 100vh;
  overflow-x: hidden;
}

header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 30px 100px;
    display: flex;
    justify-content: space-between; 
    align-items: center;
    z-index: 100;
  }
  
  .logo {
    font-size: 2em;
    color: #359381;
    pointer-events: none;
    margin-right: 0; 
  }
  
  .navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    flex: 1;
  }
  
  .navigation a {
    text-decoration: none;
    color: #359381;
    padding: 6px 15px;
    border-radius: 20px;
    font-weight: 600;
  }
  
  .navigation a:hover,
  .navigation a.active {
    background: #359381;
    color: #fff;
  }

.parallax {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: #fff !important; 
  overflow: hidden;            
  z-index: 1;
}

.parallax img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  pointer-events: none;
  z-index: 0;
}

#text {
  position: absolute;
  font-size: 5em;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .2);
  z-index: 2;
}

.grid.grid-1 {
  position: relative;
  z-index: 3;
}


.sec {
  position: relative;
  background: #003329;
  padding: 100px;
}

.sec h2 {
  font-size: 3em;
  color: #fff;
  margin-bottom: 10px;
}

.sec p {
  font-size: 1em;
  color: #fff;
  font-weight: 300;
}

.about-section {
  padding: 4rem 2rem;
  background: #f9f9f9; 
  color: #000;
}

.about-gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
}

.about-img {
  max-width: 300px;
  width: 100%;
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}


/* Section 2 */
html, body{
    scroll-behavior: smooth;
}
body{
    background-color: #CBCBCB;
    margin: 0;
    font-size: 14px;
    color: #2E373A;
    font-family: "Libre Baskerville", sans-serif;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style: none;
}
main{
    width: min(1400px, 100vw);
    margin: auto;
    padding: 20px;
}
header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
header img{
    height: 40px;
}
.banner .content{
    font-family: "Odibee Sans", sans-serif;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.banner .content h1{
    font-size: 17em;
}
.banner .content .right{
    text-align: right;
    transform: translateY(-30px);
}
.banner .content .right h2{
    font-size: 7em;
    font-weight: 200;
}
.banner .image img{
    position: absolute;
    height: 130%;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0px;
}
.grid figure img{
    width: 100%;
}

.grid{
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(var(--row), 100px);
    margin-top: 50px;
    gap: 50px;
}
.grid-1 figure:nth-child(1){
    grid-column: 2 / 6;
    grid-row: 1;
}
.grid-1 figure:nth-child(2){
    grid-column: 3 / 5;
    grid-row: 1;
}
.grid-1 h2 {
    font-size: 7em;
    grid-column: 1 / 7;
    text-align: center;
    grid-row: 1;       
    z-index: 1;
} 
.grid-1{
    --row: 2;
}

.grid-2 {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(4, auto);
    gap: 50px;
    margin-top: 50px;
}

.grid-2 .autoShow {
    grid-column: 1 / 7;
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-start;
    justify-content: space-between;
}

.grid-2 .autoShow:nth-child(1) { grid-row: 1; }
.grid-2 .autoShow:nth-child(2) { grid-row: 2; }
.grid-2 .autoShow:nth-child(3) { grid-row: 3; }
.grid-2 .autoShow:nth-child(4) { grid-row: 4; }

.grid-2 .autoShow figure img {
    width: 800px;
    height: auto;
}

.grid-2 .autoShow > div {
    flex: 1;
    margin-right: 20px;
    transform: translateY(20px);
}

.grid-2 .autoShow p {
    font-size: 6em;
    font-family: 'Odibee Sans', cursive;
    margin: 0;
}

.grid-2 .autoShow .desc {
    font-size: 2em;
    margin-top: 10px;
    color: #555;
    font-family: "Libre Baskerville", serif;
    line-height: 1.2em;
}

.grid-2 .autoShow:nth-child(2) > div,
.grid-2 .autoShow:nth-child(4) > div {
    margin-left: 100px !important;
}


.grid-3{
    --row: 6;
    font-size: 10em;
}
.grid-3 div{
    grid-column: 2/ 6;
    text-wrap: nowrap;
}
.grid-3 div:nth-child(even){
    text-align: right;
}
.grid-3 div:nth-child(5){
    grid-column: 1/ 6;
}
.grid-3 div:nth-child(4){
    grid-column: 1/ 7;
}
footer {
    border-top: 1px solid #2E373A55;
    padding: 100px 0; 
}

@media screen and (max-width: 1023px){
    header img{
        height: 2em;
    }
    .banner .content h1{
        font-size: 12em;
    }
    .banner .content .right{
        transform: none;
    }
    .grid-2 div:nth-child(1){
        grid-row: 2;
    }
    .grid-2 div:nth-child(3){
        grid-row: 6;
    }
    .grid-2 div:nth-child(4){
        grid-row: 5;
    }
    .grid-3{
        font-size: 5em;
        line-height: 1em;
    }
}
@media screen and (max-width: 1023px){
    header img{
        height: unset;
        width: 100%;
    }
    .banner .content h1{
        font-size: 7em;
    }
    .banner .content{
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .banner{
        overflow: hidden;
    }
    .grid-1 h2:nth-child(3){
        font-size: 3em;
    }
    .grid{
        display: block;
    }
    .grid-3{
        font-size: 4em;
        display: block;
        margin-bottom: 100px;
    }
}

.autoRotate{
    animation: autoRotateAnimation;
    animation-timeline: view();
}
@keyframes autoRotateAnimation{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}
.autoShow{
    animation: autoShowAnimation both;
    animation-timeline: view(70% 5%);

}
@keyframes autoShowAnimation{
    from{
        opacity: 0;
        transform: translateY(200px) scale(0.3);
    }to{
        opacity: 1;
        transform: translateY(0) scale(1);
    }
}
.autoBLur{
    animation: autoBLurAnimation linear both;
    animation-timeline: view();
}
@keyframes autoBLurAnimation{
    0%{
        filter: blur(40px);
    }
    45%, 55%{
        filter: blur(0px);
    }
    100%{
        filter: blur(40px);
    }
}


/* Section 3 */
.section-two .banner {
    width: 100%;
    height: 100vh;
    text-align: center;
    overflow: hidden;
    position: relative;
}

.section-two .banner .slider {
    position: absolute;
    width: 200px;
    height: 250px;
    top: 10%;
    left: calc(50% - 100px);
    transform-style: preserve-3d;
    transform: perspective(1000px);
    animation: autoRun 20s linear infinite;
    z-index: 2;
}

@keyframes autoRun {
    from {
        transform: perspective(1000px) rotateX(-16deg) rotateY(0deg);
    }
    to {
        transform: perspective(1000px) rotateX(-16deg) rotateY(360deg);
    }
}

.section-two .banner .slider .item {
    position: absolute;
    inset: 0;
    transform:
        rotateY(calc((var(--position) - 1) * (360 / var(--quantity)) * 1deg))
        translateZ(550px);
}

.section-two .banner .slider .item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.section-two .banner .content {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: min(1400px, 100vw);
    height: max-content;
    padding-bottom: 100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    z-index: 1;
}

.section-two .banner .content h1 {
    font-family: 'ICA Rubrik';
    font-size: 16em;
    line-height: 1em;
    color: #25283B;
    position: relative;
}

.section-two .banner .content h1::after {
    position: absolute;
    inset: 0;
    content: attr(data-content);
    z-index: 2;
    -webkit-text-stroke: 2px #d2d2d2;
    color: transparent;
}

.section-two .banner .content .author {
    font-family: Poppins;
    text-align: right;
    max-width: 200px;
}

.section-two .banner .content h2 {
    font-size: 3em;
}

@media screen and (max-width: 1023px) {
    .section-two .banner .slider {
        width: 160px;
        height: 200px;
        left: calc(50% - 80px);
    }
    .section-two .banner .slider .item {
        transform:
            rotateY(calc((var(--position) - 1) * (360 / var(--quantity)) * 1deg))
            translateZ(700px);
    }
    .section-two .banner .content h1 {
        text-align: center;
        width: 100%;
        text-shadow: 0 10px 20px #000;
        font-size: 7em;
    }
    .section-two .banner .content .author {
        color: #fff;
        padding: 0 30px;
        text-shadow: 0 10px 20px #000;
        z-index: 2;
        width: 100%;
        text-align: center;
    }
}

@media screen and (max-width: 767px) {
    .section-two .banner .slider {
        width: 100px;
        height: 150px;
        left: calc(50% - 50px);
    }
    .section-two .banner .slider .item {
        transform:
            rotateY(calc((var(--position) - 1) * (360 / var(--quantity)) * 1deg))
            translateZ(180px);
    }
    .section-two .banner .content h1 {
        font-size: 5em;
    }
}


/* Section 4 */
html, body {
    scroll-behavior: smooth;
  }
  
  * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style: none;
  }
  
  .location-wrapper {
    background-color: #1C1C1C;
    color: #fff;
    font-family: "Libre Baskerville", sans-serif;
    padding: 40px 20px;
  }
  
  .location-wrapper main {
    width: min(1400px, 100vw);
    margin: auto;
  }
  
  .location-wrapper .grid-3 {
    --row: 2;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(var(--row), 100px);
    margin-top: 50px;
    margin-bottom: 200px;
  }
  
  .location-wrapper .grid-3 h2 {
    font-size: 2em;
    grid-column: 1 / 7;
    text-align: center;
    grid-row: 1;
    z-index: 1;
    font-family: 'Odibee Sans', cursive;
  }
  
  .location-wrapper .grid-4 {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: auto;
    gap: 50px;
    margin-top: 50px;
  }
  
  .location-wrapper .grid-4 .autoShow {
    grid-column: 1 / 7;
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-start;
    justify-content: space-between;
  }
  
  .location-wrapper .grid-4 .autoShow figure {
    flex: 0 1 50%;
    max-width: 600px;
    height: 300px;
    margin-left: 10px;
  }
  
  .location-wrapper .grid-4 .autoShow iframe {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    box-shadow: 0 10px 30px rgba(0,0,0,0.15);
    border: 0;
  }
  
  .location-wrapper .grid-4 .autoShow > div {
    flex: 0 1 50%;
    margin-right: 10px;
    transform: translateY(20px);
  }
  
  .location-wrapper .grid-4 .autoShow p {
    font-size: 6em;
    font-family: 'Odibee Sans', cursive;
    margin: 0;
  }
  
  .location-wrapper .grid-4 .autoShow .desc {
    font-size: 2em;
    margin-top: 10px;
    color: #fff;
    font-family: "Libre Baskerville", serif;
    line-height: 1.4em;
  }
  
  /* Responsive Layout */
  @media screen and (max-width: 1023px) {
    .location-wrapper .grid-3 h2 {
      font-size: 2.5em;
    }
  
    .location-wrapper .grid-4 {
      display: block;
      margin-top: 40px;
    }
  
    .location-wrapper .grid-4 .autoShow {
      flex-direction: column;
      text-align: center;
    }
  
    .location-wrapper .grid-4 .autoShow > div {
      margin-right: 0;
      transform: none;
    }
  
    .location-wrapper .grid-4 .autoShow p {
      font-size: 2em;
    }
  
    .location-wrapper .grid-4 .autoShow .desc {
      font-size: 1.2em;
    }
  
    .location-wrapper .grid-4 .autoShow figure {
      width: 100%;
      height: auto;
      margin-left: 0;
    }
  
    .location-wrapper .grid-4 .autoShow iframe {
      height: 300px;
    }
  }
  
  /* Animation */
  .location-wrapper .autoShow {
    animation: autoShowAnimation both;
    animation-timeline: view(70% 5%);
  }
  
  @keyframes autoShowAnimation {
    from {
      opacity: 0;
      transform: translateY(200px) scale(0.3);
    }
    to {
      opacity: 1;
      transform: translateY(0) scale(1);
    }
  }
  
  /* SECTION NAVIGATION BAR */
  .section-nav {
    position: sticky;
    top: 0;
    background-color: #e0e0e0;
    padding: 12px 0;
    z-index: 1000;
    border-bottom: 1px solid #ccc;
    box-shadow: 0 1px 4px rgba(0,0,0,0.1);
  }
  
  .section-nav ul {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
    margin: 0;
    padding: 0;
  }
  
  .section-nav a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
    font-family: "Libre Baskerville", serif;
    font-size: 1rem;
    position: relative;
    transition: color 0.3s ease;
  }
  
  .section-nav a:hover,
  .section-nav a:focus {
    color: #000;
  }
  
  .section-nav a::after {
    content: "";
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 0%;
    height: 2px;
    background-color: #333;
    transition: width 0.3s ease-in-out;
  }
  
  .section-nav a:hover::after,
  .section-nav a:focus::after {
    width: 100%;
  }
  
  section {
    scroll-margin-top: 80px;
  }
  
  .footer-nav {
    background-color: #f0f0f0;
    padding: 30px 0;
  }
  
  .footer-nav__container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    gap: 60px;
  }
  
  .footer-nav__item {
    text-decoration: none;
    color: #333;
    font-family: "Libre Baskerville", serif;
    font-size: 1.1rem;
    font-weight: bold;
    position: relative;
    padding: 8px 0;
  }
  
  .footer-nav__item::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -4px;
    width: 0;
    height: 2px;
    background: #333;
    transition: width 0.3s ease;
  }
  
  .footer-nav__item:hover::after,
  .footer-nav__item:focus::after {
    width: 100%;
  }
  
  :root {
      --section-padding: 60px;
      --main-color: #10cab7;
      --secondary-color: #2c4755;
    }
    
    body {
      margin: 0;
      font-family: Arial, sans-serif;
    }
    
    .container {
      padding-left: 15px;
      padding-right: 15px;
      margin-left: auto;
      margin-right: auto;
      max-width: 1200px;
    }
    
    /* Contact Section */
  .contact {
      padding-top: var(--section-padding);
      padding-bottom: var(--section-padding);
      background-color: white;
    }
    
    .special-heading {
      text-align: center;
      font-size: 100px;
      color: #1C1C1C;
      font-weight: 900;
      letter-spacing: -3px;
      margin: 0;
      opacity: 0.2;
    }
    
    .contact p {
      text-align: center;
      font-size: 18px;
      color: #797979;
      margin: -20px 0 0;
    }
    
    .contact .info {
      padding-top: 60px;
      padding-bottom: 60px;
      text-align: center;
    }
    
    .contact .info .label {
      font-size: 35px;
      font-weight: 800;
      color: var(--secondary-color);
      letter-spacing: -2px;
      margin-bottom: 15px;
    }
    
    .contact .info .link {
      display: block;
      font-size: 35px;
      font-weight: 800;
      color: var(--main-color);
      text-decoration: none;
      margin: 10px 0;
    }
    
    .contact .info .social {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 20px;
      font-size: 16px;
      color: var(--secondary-color);
    }
    
    .contact .info .social a {
      text-decoration: none;
      margin-left: 10px;
      color: var(--secondary-color);
      transition: color 0.3s ease;
    }
    
    .contact .info .social a:hover {
      color: var(--main-color);
    }
    
    .contact .info .social i {
      cursor: pointer;
      transition: color 0.3s ease;
    }
    
    .contact .info .social .social-icon {
      width: 24px;
      height: 24px;
      vertical-align: middle;
      transition: opacity 0.3s ease;
    }
    
    .contact .info .social a:hover .social-icon {
      opacity: 0.8;
    }
    
    @media (max-width: 767px) {
      .special-heading {
        font-size: 60px;
      }
      .contact .info .label,
      .contact .info .link {
        font-size: 25px;
      }
    }
    
    /* Footer */
    .footer {
      background-color: var(--secondary-color);
      color: white;
      padding: 30px 10px;
      text-align: center;
      font-size: 18px;
    }
    
    .footer span {
      font-weight: bold;
      color: var(--main-color);
    }
